/* HEADER CONTAINER */
.nav-container {
  // nav bar is fixed on top
  position: fixed;
  width: 100%;
  top: 0;
  // prevent scrollbar appearing on top
  overflow: hidden;
  background-color: #CDD5D1; }
/* NAVIGATION BAR */
nav.nav-bar {
  position: relative;
  transition: all 0.3s ease-in;
  max-width: 1000px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  padding: 0 20px; }
@media screen and (max-width: 600px) {
  padding: 35px 20px; }
/* WEBSITE LOGO */
a.nav-logo {
  display: inline-block;
  height: 50px;
  width: 50px;
  background: {
    image: url('../img/logostandin.png');
    size: cover;
    position: center center;
    repeat: no-repeat; } }
/* NAVIGATION LIST */
ul.navigation {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 600px) {
    display: none; }
  li {
    display: block;
    padding-right: 30px;
    a {
      color: $white;
      transition: all 0.15s ease-in; }
    a:hover {
      color: darken($white, 25%); } }
  li:last-of-type {
    //Removes padding on last nav link
    padding-right: 0; } }
ul.navigation.nav-dark {
  li {
    a {
      color: $primary; }
    a:hover {
      color: darken($white, 25%); } } }
